* {
  font-size: 24px;
  font-family: Arial, Helvetica, sans-serif;
  color: rgb(255, 255, 255);
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

::-webkit-scrollbar { 
  display: none; 
}

ul {
  list-style: none;
}

button {
  user-select: none;
  outline: none;
  cursor: pointer;
  color: black;
  border-radius: 6px;
  background: skyblue;
  padding: 0 10px;
}

button.disabled {
  cursor: default;
  background: grey;
  pointer-events: none;
}


input {
  cursor: pointer;
}

button:hover,
button:focus {
  outline: 0;
}

html {
  height: 100vh;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  row-gap: 20px;
  background-image: none;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-blend-mode: multiply;
  background-color: rgb(0, 0, 0);
  & > .route-panel {
    display: flex;
    column-gap: 20px;
  }
  & > .app-container {
    display: flex;
    width: 100%;
    height: 100%;
  }
}

.garage {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 20px;
  row-gap: 40px;
  .control-panel {
    display: flex;
    flex-direction: column;
    row-gap: 15px;
    width: 100%;
    & > .create-panel,
    & > .update-panel,
    & > .race-panel {
      display: flex;
      align-items: center;
      column-gap: 20px;
    }
    .generate-button {
      width: 200px;
    }
  }
}

.car-list {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  width: 100%;
  .car-item {
    display: flex;
    flex-direction: column;
    .car-controls {
      display: flex;
      column-gap: 10px;
    }
    .car-title {
      color: white;
    }
    .car-container {
      width: 100%;
      display: flex;
      position: relative;
      height: 45px;
      border-bottom: 4px dashed white;

      .car {
        display: block;
        position: absolute;
        width: 60px;
        height: 40px;
        bottom: -10px;
        background-size: contain;
        background-repeat: no-repeat;
      }
    }
  }
}

.garage-route,
.winners-route {
  width: 180px;
}

.page-description {
  margin: 0 10px;
  width: 180px;
}

.winners-container {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  padding: 20px;
}

.winners-table {
  display: flex;
  flex-direction: column;
  font-size: 16px;
}

.winners-heading,
.winners-item {
  display: flex;
}

.winners-list {
  display: flex;
  flex-direction: column;
}

.winner-number, 
.winner-car, 
.winner-name,
.winner-wins, 
.winner-time {
  display: flex;
  align-items: center;
}

.winner-number { width: 10% }
.winner-car { width: 20% }
.winner-name { width: 40% }
.winner-wins { width: 10% }
.winner-time { width: 20% }

.winners-heading > .winner-number,
.winners-heading > .winner-car,
.winners-heading > .winner-name,
.winners-heading > .winner-wins,
.winners-heading > .winner-time {
  display: flex;
  align-items: center;
  background: rgb(77, 95, 253);
  font-size: 18px;
}

.winners-item > .winner-number,
.winners-item > .winner-car,
.winners-item > .winner-name,
.winners-item > .winner-wins,
.winners-item > .winner-time {
  font-size: 16px;
}

.clickable {
  user-select: none;
  cursor: pointer;
  transition: background ease 0.3s;
}

.clickable:hover {
  background: skyblue;
}

.popup-wrapper {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
  width: 100vw;
  height: 100vh;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.5);
}
.popup-wrapper.active {
  cursor: pointer;
  opacity: 1;
}

.popup {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 480px;
  padding: 50px 15px;
  background-color: rgba(49, 49, 49, 0.75);
  border-radius: 10px;
  backdrop-filter: blur(5px);
  transform: translateY(-200px);
  opacity: 0;
}
.popup.active {
  transform: translateY(0);
  opacity: 1;
}
.popup-exit-button {
  cursor: pointer;
  position: relative;
  display: block;
  width: 30px;
  height: 30px;
}
.popup-exit-button::before {
  content: '';
  position: absolute;
  display: block;
  top: calc(50% - 2px);
  width: 100%;
  height: 4px;
  transform: rotate(45deg);
  background-color: white;
  transition: background ease 150ms;
}
.popup-exit-button::after {
  content: '';
  position: absolute;
  display: block;
  top: calc(50% - 2px);
  width: 100%;
  height: 4px;
  transform: rotate(-45deg);
  background-color: white;
  transition: background-color ease 150ms;
}
.popup > .exit-button:hover::after,
.popup > .exit-button:hover::before {
  background-color: skyblue;
}